.right-wrapper {
  position: absolute;
  right: 20px;
  top: 20px;
  display: grid;
  grid-gap: 10px;
  z-index: 3;
}
.right-item {
  width: 45px;
  height: 45px;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  padding: 5px;
  border: 2px solid rgba(33, 154, 208, 1);
  display: grid;
  cursor: pointer;
  img {
    width: 27px;
    height: 27px;
    align-self: center;
    justify-self: center;
  }
}
.right-item:hover {
  background-color: rgba(175, 175, 175, 0.4);
}
.map-filters-button {
  position: absolute;
  top: 20px;
  right: 20px;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 1);
  cursor: pointer;
  display: grid;
  padding: 8px;
  border: 2px solid rgba(126, 126, 132, 0.4)
}
.map-filters-button:hover {
  background-color: rgba(243, 243, 243, 1);
}