.left-item {
	width: 45px;
	height: 45px;
	background: rgba(33, 154, 208, 0.5);
	border-radius: 50%;
	padding: 5px;
	border: 2px solid rgba(255, 255, 255, 1);
	display: grid;
	img {
		width: 35px;
		height: 35px;
		align-self: center;
		justify-self: center;
	}
}
@media screen and (max-width: 768px) {
	.left-item {
		width: 35px;
		height: 35px;
		img {
			width: 30px;
			height: 30px;
		}
	}
}