.poi-filters-wrapper {
	position: absolute;
	right: 100px;
	top: 20px;
	display: grid;
	grid-template-columns: repeat(5, 1fr);
	background-color: rgba(255, 255, 255, 1);
	padding: 20px;
	z-index: 5;
	border-radius: 20px;
	grid-column-gap: 20px;
	grid-row-gap: 20px;
}
@media screen and (max-width: 768px) {
	.poi-filters-wrapper {
		grid-template-columns: repeat(3, 1fr);
		right: 90px;
		grid-column-gap: 10px;
		grid-row-gap: 10px;
	}
}