.filters-item {
	width: 55px;
	height: 55px;
	background: rgba(255, 255, 255, 1);
	border-radius: 50%;
	padding: 5px;
	display: grid;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
	img {
		width: 35px;
		height: 35px;
		align-self: center;
		justify-self: center;
	}
}
@media screen and (max-width: 768px) {
	.filters-item {
		width: 35px;
		height: 35px;
		img {
			width: 30px;
			height: 30px;
		}
	}
}